@media screen and (max-width: 1800px) {
  .whyYouNeedThat {
      h2 {
        line-height: 1.5; } } }

@media screen and (max-width: 1024px) {
  .whyYouNeedThat {
    p {
      font-size: 16px;
      &:first-child {
        font-size: 17px;
        padding-left: 25px;
        padding-top: 25px; } }
    &__left {
      padding: 10px 0; }
    &__slider {
      top: 14.9vw;
      p {
        font-size: 15px; }
      .uber {
        padding-top: 0; } }
    .uberSVG {
      width: 80px; } } }
@media screen and (max-width: 768px) {
  .whyYouNeedThat {
    &__slider {
      top: 25vw; } } }
@media screen and (max-width: 500px) {
  .whyYouNeedThat {
    background-image: linear-gradient(180deg,#001d99 130vw,#fafcfc 130vw);
    &__wrapper {
      grid-template-columns: 1fr;
      grid-template-rows: 1fr 1fr;
      grid-template-areas: 'left''right';
      padding-bottom: 30vw; }
    &__left {
      grid-area: left;
      h2 {
        line-height: 15vw; }
      p {
        width: 100%;
        padding-top: 7vw;
        font-size: 4.8vw;
        line-height: 26px; } }
    &__right {
      grid-area: right;
      span {
        font-size: 5.25vw;
        padding: 12vw 0;
        line-height: 26px; }
      p:first-child {
        padding-left: 0; } }
    & .uberSVG {
       height: 25px; }
    h2 {
      font-size: 9.5vw;
      &:after {
        bottom: -8vw;
        width: 25vw;
        height: 3px; } }
    &__slider {
      top: 80vw;
      img {
        max-height: 9.08333vw;
        height: 40px; }
      p {
        font-size: 14px;
        line-height: 20px;
        color: #9ab4c1; } }
    .slider__item {
      margin-bottom: 10px; }
    .partCircle {
      bottom: 153vw;
      width: 63vw;
      &SM {
        right: -1vw;
        top: .2vw;
        width: 15vw; } }


    //slider
    .slick-prev, .slick-next {
      top: 84vw;
      width: 17vw;
      height: 17vw; }
    .slider__item {
      margin-left: 0; }
    .slick-next {
      left: 75vw; }
    .slick-prev {
      left: 58vw;
      background: url(../img/svg/left-arrow.svg), #fafcfc;
      background-size: 35%;
      background-repeat: no-repeat;
      background-position: center center; } } }
